<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
  >

    <v-card :loading="data.loading" :disabled="data.loading" class="pass-form">
      <a-card-title>
        <span>جزئیات پاسخ ها</span>
        <span v-if="comment.content && comment.content.length>30" style="font-size: 14px;margin-right: 8px"> ({{  comment.content.substring(0,30) + '...' }}) </span>
        <span v-else-if="comment.content" style="font-size: 14px;margin-right: 8px"> ({{ comment.content }}) </span>
      </a-card-title>

      <v-card-text style="min-height: 200px;max-height: 500px;overflow: auto" class="pt-6">

        <v-form ref="form">

          <div class="mb-4 pa-3" :class="[i%2===0?'grey lighten-4':'']" v-for="(item,i) in comment.comment_replies"
               :key="i+'cryplies'">
            <div class="d-flex justify-space-between" >
              <p style="font-size: 12px" class="mb-2"> وضعیت : <span v-if="item.enabled"
                                                                     style="color: darkgreen">فعال <v-btn @click="changeStatus(item.id,i)" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn></span><span v-else-if="item.enabled===false"
                                                                                                                                                                                                            style="color: darkred">غیر فعال <v-btn @click="changeStatus(item.id,i)" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn></span>
              </p>
              <p style="font-size: 12px" v-if="item && item.user && item.user.username" >کاربر : {{ comment.user.username }}</p>
            </div>
            <p v-if="commentSelected.id!==item.id" >{{ item.content }} <v-btn @click="selectComment(item)" small icon color="blue darken-2" ><v-icon small color="blue darken-2">mdi-pencil</v-icon></v-btn></p>
            <v-textarea
                hide-details
                :disabled="!($store.state.permissions && $store.state.permissions['change-comment'])"
                v-if="commentSelected.id===item.id"
                v-model="commentSelected.content"
            >

              <template v-slot:append >
                <v-btn @click="editComment()" icon color="blue darken-2"><v-icon color="green darken-2">mdi-check</v-icon></v-btn>
              </template>
            </v-textarea>
            <v-divider></v-divider>
          </div>

        </v-form>

      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>

      </v-card-actions>
      <template v-slot:progress>
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>

import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";

export default {
  name: "DashReplyComments",
  components: {ACardTitle, GDLoading},
  props: ["cdata"],
  data() {
    return {
      data: this.cdata,
      readonly: true,
      comment: {},
      commentSelected : {
        id : null,
        content : null,
      }
    }
  },methods : {
    changeStatus (id,index) {
      const date_published_date = new Date().getTime()/1000;
      this.comment.comment_replies[index].enabled = !this.comment.comment_replies[index].enabled;
      axios.patch(this.$store.state.host + '/blog/api/v1/comment/change-comment/'+id,{
        enabled : this.comment.comment_replies[index].enabled,
        date_published_date : parseInt(date_published_date)
      },{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(()=>{
        this.$emit('deleteFn');
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    selectComment (item) {
      this.commentSelected = item;
    },
    editComment () {
      if(this.commentSelected.content) {
        this.data.loading = true;
        let commentSelect = this.commentSelected;
        this.commentSelected = {
          id : null,
          content : null,
        };
        axios.patch(this.$store.state.host + '/blog/api/v1/comment/change-comment/'+commentSelect.id,{
          content : commentSelect.content
        },{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(()=>{
          this.notify('کامنت ویرایش شد.','info');
          this.fetchComment();
        }).catch(error=>{
          this.errorHandle(error.response)
        })
      }
    },
    fetchComment(){
      axios.get(this.$store.state.host + '/blog/api/v1/comment/comment-detail/' + this.data.idSelected, {
        headers: {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(res => {
        this.comment = res.data.data
      }).catch(error => {
        this.errorHandle(error.response)
      }).finally(() => {
        this.data.loading = false;
    })
    }
  },
  watch: {
    'data.dialog'(val) {
      this.data.loading = true;
      this.comment = {};
      if (val) {
        this.fetchComment();
      }
    }
  }
}
</script>

