<template>
  <v-dialog
      v-model="data.dialog"
      :persistent="data.loading"
  >

    <v-card :loading="data.loading" :disabled="data.loading" class="pass-form">
      <a-card-title>
        <span>جزئیات نظر</span>
      </a-card-title>

      <v-card-text style="min-height: 200px" class="pt-6">

        <v-form ref="form">

          <div class="d-flex justify-space-between" >
            <p style="font-size: 12px" class="mb-2"> وضعیت : <span v-if="comment.enabled"
                                                                   style="color: darkgreen">فعال <v-btn @click="changeStatus()" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn></span><span v-else-if="comment.enabled===false"
                                                                                                             style="color: darkred">غیر فعال <v-btn @click="changeStatus()" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn></span>
            </p>
            <p style="font-size: 12px" v-if="comment && comment.user && comment.user.username" >کاربر : {{ comment.user.username }}</p>
          </div>



          <v-row class="pa-0 py-6">

            <v-textarea
            v-model="comment.content"
            :disabled="!($store.state.permissions && $store.state.permissions['change-comment'])"
            >

              <template v-slot:append >
                <v-btn @click="changeComment()" icon color="blue darken-2"><v-icon color="green darken-2">mdi-check</v-icon></v-btn>
              </template>

            </v-textarea>

          </v-row>
        </v-form>

      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
      </v-card-actions>
      <template v-slot:progress >
        <GDLoading/>
      </template>
    </v-card>
  </v-dialog>
</template>

<script>
import axios from "axios";
import GDLoading from "@/components/Dashboard/Misc/GDLoading";
import ACardTitle from "@/components/Dashboard/Misc/Components/a-card-title";

export default {
  name: "DashCommentDetail",
  components: {ACardTitle, GDLoading},
  props : ["cdata"],
  data () {
     return {
       data : this.cdata,
       readonly : true,
       comment : {
         content : null,
       },
     }
  },
  methods : {
    changeComment () {
      this.$refs.form.validate();
      if(this.$refs.form.validate()) {
        this.data.loading = true;
        axios.patch(this.$store.state.host + '/blog/api/v1/comment/change-comment/'+this.data.idSelected, {
          content : this.comment.content
        },{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(()=>{
          this.notify('کامنت ویرایش شد.','info');
          this.$emit('deleteFn');
          this.data.dialog = false;
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.data.loading = false;
        })
      }
    },
    changeStatus () {
      this.comment.enabled = !this.comment.enabled;
      axios.patch(this.$store.state.host + '/blog/api/v1/comment/change-comment/'+this.data.idSelected,{
        enabled : this.comment.enabled
      },{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(()=>{
        this.$emit('deleteFn');
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    }
  },

  watch : {
    'data.dialog'(val){
      this.data.loading = true;
      this.comment = {
        content : null
      };
      if(val) {
        this.readonly = true;
        axios.get(this.$store.state.host + '/blog/api/v1/comment/comment-detail/'+this.data.idSelected,{
          headers : {
            Authorization: `Bearer ${this.$store.state.userId}`,
          }
        }).then(res=>{
          this.comment = res.data.data
        }).catch(error=>{
          this.errorHandle(error.response)
        }).finally(()=>{
          this.data.loading = false;
        })
      }
    }
  }
}
</script>

