<template>
  <v-card style="background-color: transparent" elevation="0" :disabled="loading">
    <v-row >
      <v-col md="12" cols="12">
        <BlockItem>
          <span slot="title">مدیریت نظرات</span>
          <template slot="icon">mdi-comment</template>
          <v-data-table
              hide-default-footer
              :headers="headers" :loading="loading" :items="data.data" item-key="id" disable-sort
          >

            <template v-slot:[`item.content`]="{item}">
                <a class="secondary--text" @click="toDetail(item.id)" v-if="item.content.length>30">{{item.content.substring(0,30) + '...'}}</a>
                <a class="secondary--text" @click="toDetail(item.id)" v-else>{{item.content}}</a>
            </template>

            <template v-slot:[`item.date_created_date`]="{item}">
                {{item.date_created_date | momentHour}}
            </template>

            <template v-slot:[`item.enabled`]="{item}">
              <v-btn @click="changeStatus(item.id,item.enabled)" icon x-small ><v-icon x-small >mdi-autorenew</v-icon></v-btn>
              <span class="table-flag-enabled table-flag-enabled--green" v-if="item.enabled" >فعال</span>
              <span class="table-flag-enabled table-flag-enabled--red" v-else>غیر فعال</span>
            </template>

            <template v-slot:[`item.post`]="{item}">
              <a class="secondary--text"  @click="toPostView(item.post.id)" v-if="item.post.title.length>30">{{item.post.title.substring(0,30) + '...'}}</a>
              <a class="secondary--text"  @click="toPostView(item.post.id)" v-else>{{item.post.title}}</a>

            </template>

            <template v-slot:[`item.id`]="{item}">
              <div class="d-flex justify-center">
                <a-sbtn v-if="item && item.comment_replies && item.comment_replies.length" @clickBtn="toReplysDetail(item.id)" color="#6c757d" icon="mdi-comment-processing" alt="پاسخ ها" />
                <div v-else style="width: 40px" ></div>
                <a-sbtn @clickBtn="toDetail(item.id)" color="#17a2b8" icon="mdi-comment" alt="جزئیات نظر" />
                <a-sbtn v-if="$store.state.permissions && $store.state.permissions['delete-comment']" @clickBtn="toDelete(item.id,'/blog/api/v1/comment/delete-comment/')" color="#DC3545" icon="mdi-delete" alt="حذف نظر" />
              </div>
            </template>


          </v-data-table>


          <v-pagination
              v-if="data.total_objects"
              :value="options.page"
              v-model="options.page"
              color="info"
              class="mt-6"
              :length="parseInt(data.total_pages)"
          ></v-pagination>
          <p class="text-center  mb-2 mt-2 info--text" v-if="data.total_objects" style="font-size: 14px">
            {{ data.total_objects }} نظر</p>


        </BlockItem>
      </v-col>
    </v-row>
    <DeleteDialog :ddata="deleteData" @deleteFn="fetchComments()" ></DeleteDialog>
    <DashCommentDetail :cdata="commentData" @deleteFn="fetchComments()" ></DashCommentDetail>
    <DashReplyComments :cdata="commentReplyData" @deleteFn="fetchComments()" ></DashReplyComments>
    <PostView :cdata="postData" ></PostView>
  </v-card>
</template>

<script>
import axios from "axios";
import BlockItem from "@/components/Misc/BlockItem";
import DeleteDialog from "@/components/Misc/DeleteDialog";
import DashCommentDetail from "@/components/Dashboard/DashComments/DashCommentDetail";
import DashReplyComments from "@/components/Dashboard/DashComments/DashReplyComments";
import PostView from "@/components/Dashboard/Misc/PostView";
import ASbtn from "@/components/Dashboard/Misc/Components/a-sbtn";

export default {
  name: "DashComments",
  components : {
    ASbtn,
    PostView,
    DashReplyComments,
    DashCommentDetail,
    DeleteDialog,
    BlockItem
  },
  watch: {
    'options.page'() {
      this.fetchComments();
    }
  },
  created() {
    this.fetchComments();
  },
  data () {
    return {
      options : {
        page : 1,
        ordering: '-id'
      },
      loading : false,
      data: [],
      deleteData: {
        api : '',
        dialog: false,
        loading: false,
        idSelected: null,
      },
      commentData : {
        dialog: false,
        loading: false,
        idSelected: null,
      },
      commentReplyData : {
        dialog: false,
        loading: false,
        idSelected: null,
      },
      postData : {
        dialog: false,
        loading: false,
        idSelected: null,
      }
    }
  },
  computed : {
    headers() {
      return [
        {
          text: 'متن نظر',
          align: 'start',
          class: 'header-table-gray',
          sortable: false,
          value: 'content',
        },
        {
          text: 'فعال', class: 'header-table-gray',
          align: 'center',
          value: 'enabled',
        },
        {
          text: 'پست', class: 'header-table-gray',
          align: 'center',
          value: 'post',
        },
        {
          text: 'کاربر', class: 'header-table-gray',
          align: 'center',
          value: 'user.username',
        },
        {
          text: 'تاریخ ایجاد', class: 'header-table-gray',
          align: 'center',
          value: 'date_created_date'
        },
        {
          text: 'عملیات', class: 'header-table-gray',
          align: 'center',
          value: 'id',
        },


      ]
    },
  },
  methods : {
    changeStatus (id,enabled) {
      enabled = !enabled;
      const date_published_date = new Date().getTime()/1000;
      axios.patch(this.$store.state.host + '/blog/api/v1/comment/change-comment/'+id,{
        enabled,
        date_published_date : parseInt(date_published_date)
      },{
        headers : {
          Authorization: `Bearer ${this.$store.state.userId}`,
        }
      }).then(()=>{
        this.fetchComments();
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    },
    toDelete(id,api) {
      this.deleteData.idSelected = id;
      this.deleteData.api = api;
      this.deleteData.dialog = true;
    },
    toDetail(id) {
      this.commentData.idSelected = id;
      this.commentData.dialog = true;
    },
    toPostView(id) {
      this.postData.idSelected = id;
      this.postData.dialog = true;
    },
    toReplysDetail (id) {
      this.commentReplyData.idSelected = id;
      this.commentReplyData.dialog = true;
    },

    fetchComments () {
      this.loading = true;
      axios.get(this.$store.state.host + '/blog/api/v1/comment/comments',{
        params : this.options
      }).then(response=>{
        this.data = response.data;
        console.log(this.data)
        this.loading = false;
      }).catch(error=>{
        this.errorHandle(error.response)
      })
    }
  }
}
</script>

